import React from 'react'

import "../assets/css/applications.css"

function Userlogin() {
    const popup = {

        "max-width": "600px"
    }

    const width = {
        width: "90%"
    }
    const clr = {
        "background-image": "linear-gradient(to right, rgb(255, 84, 0), #f7ff00)",
        "border": "none"
    }
    const bg = {
        "background-color": "rgb(0,0,0,0)"

    }
    const h2 = {
        color: "#000",
    }
    return (
        <>
            <div class="view" style={{ "margin-left": " 0px" }}>

                <section class="admin_option" style={popup}>

                    <section class="admin_approval_forms" style={clr}>
                        <div class="admin_option_head" style={bg}>
                            <h2 style={h2}><i class="fas fa-user-tie"></i>  User Login</h2>
                        </div>
                        <hr />

                        <form class="modal-content">
                            <div class="input_items" style={width}>

                                <label for="First Name">First Name :</label>

                                <input type="text" placeholder="Enter First Name" name="First Name" required />


                            </div>

                            <div class="input_items" style={width}>
                                <label for="Last Name">Last Name :</label>
                                <div class="inputs">

                                    <input type="text" placeholder="Enter Last Name" name="Last Name" required />
                                </div>


                            </div>



                            <div class="input_items" style={width}>

                                <label for="Epic Number"> Epic Number :</label>
                                <div class="inputs">

                                    <input type="text" placeholder="Epic Number(ABC0000000-ABC9999999)"
                                        name="Epic Number" required />
                                </div>
                            </div>
                            <div class="input_items" style={width}>

                                <label for="DOB">Date Of Birth :</label>

                                <input type="date" placeholder="Enter DOB" name="DOB" required />
                            </div>

                            <div class="input_items" style={width}>

                                <label for="password">Password :</label>

                                <input type="password" placeholder="Enter Password" name="password" required />
                            </div>





                            <div class="input_cluster" id="admin_input_cluster">
                                <div id="remove_button">

                                    <input type="submit" value="Login" />


                                </div>
                                <div class="input_item_button">
                                    <input type="reset" value="Reset" />

                                </div>


                            </div>

                        </form>

                    </section>
                </section>
            </div>
        </>
    )
}

export default Userlogin